import React from 'react';
import { Modal } from 'bootstrap';
import './VideoModal.css';

const VideoModal = ({ isOpen, onClose, videoSrc, videoTitle }) => {
  const modalRef = React.useRef(null);
  const modalInstance = React.useRef(null);

  React.useEffect(() => {
    // Inicializa o modal quando o componente é montado
    if (modalRef.current) {
      modalInstance.current = new Modal(modalRef.current, {
        backdrop: 'static',
        keyboard: false
      });
    }

    // Limpa o modal quando o componente é desmontado
    return () => {
      if (modalInstance.current) {
        modalInstance.current.dispose();
      }
    };
  }, []);

  React.useEffect(() => {
    // Controla o modal com base na prop isOpen
    if (isOpen && modalInstance.current) {
      modalInstance.current.show();
    } else if (!isOpen && modalInstance.current) {
      modalInstance.current.hide();
    }
  }, [isOpen]);

  // Lida com o evento hidden do modal para sincronizar com o estado do React
  React.useEffect(() => {
    const modalElement = modalRef.current;
    
    const handleHidden = () => {
      if (onClose) onClose();
    };
    
    if (modalElement) {
      modalElement.addEventListener('hidden.bs.modal', handleHidden);
    }
    
    return () => {
      if (modalElement) {
        modalElement.removeEventListener('hidden.bs.modal', handleHidden);
      }
    };
  }, [onClose]);

  return (
    <div 
      className="modal fade" 
      id="videoModal" 
      tabIndex="-1" 
      aria-labelledby="videoModalLabel" 
      aria-hidden="true"
      ref={modalRef}
    >
      <div className="modal-dialog modal-dialog-centered modal-lg">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title" id="videoModalLabel">{videoTitle}</h5>
            <button 
              type="button" 
              className="btn-close" 
              aria-label="Fechar"
              onClick={onClose}
            ></button>
          </div>
          <div className="modal-body">
            <div className="video-container">
              {videoSrc && (
                <video controls className="video-player">
                  <source src={videoSrc} type="video/mp4" />
                  Seu navegador não suporta a tag de vídeo.
                </video>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default VideoModal; 