    thumbnail: "https://images.pexels.com/photos/841228/pexels-photo-841228.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
    videoSrc: "https://static.videezy.com/system/resources/previews/000/039/700/original/abstract-shape4k.mp4",
