import React from 'react';
import { motion } from 'framer-motion';

const ModernHero = () => {
  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.3
      }
    }
  };

  const itemVariants = {
    hidden: { opacity: 0, x: -50 },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        type: "spring",
        stiffness: 100,
        damping: 15
      }
    }
  };

  const buttonVariants = {
    hidden: { opacity: 0, y: 20, scale: 0.8 },
    visible: {
      opacity: 1,
      y: 0,
      scale: 1,
      transition: {
        delay: 0.8,
        type: "spring",
        stiffness: 200,
        damping: 15
      }
    },
    hover: {
      scale: 1.05,
      boxShadow: "0 15px 25px rgba(220, 53, 69, 0.5)",
      y: -3,
      transition: {
        type: "spring",
        stiffness: 400,
        damping: 10
      }
    },
    tap: {
      scale: 0.98,
      y: 1
    }
  };

  return (
    <motion.div 
      className="hero-text-wrapper"
      initial="hidden"
      animate="visible"
      variants={containerVariants}
    >
      <motion.h1 className="modern-hero-title" variants={itemVariants}>
      Transformamos ideias em experiências digitais <br />
        <motion.span 
          className="highlight"
          initial={{ width: 0 }}
          animate={{ width: "100%" }}
          transition={{ delay: 1, duration: 0.8 }}
        >
          inesquecíveis
        </motion.span>
      </motion.h1>
      
      <motion.p 
        className="modern-hero-subtitle"
        variants={itemVariants}
      >
        Marketing estratégico e produção audiovisual de alto nível para conectar sua marca ao público certo
      </motion.p>
      
      <motion.button 
        className="btn btn-danger cta-button modern-cta"
        variants={buttonVariants}
        whileHover="hover"
        whileTap="tap"
      >
        Entrar em contato
      </motion.button>
    </motion.div>
  );
};

export default ModernHero; 