import React, { useState, useEffect, useRef } from 'react';

const CounterAnimation = ({ end, duration = 2000, suffix = '' }) => {
  const [count, setCount] = useState(0);
  const countRef = useRef(null);
  const observerRef = useRef(null);
  const hasAnimated = useRef(false);

  // Formata o número com vírgulas para milhares
  const formatNumber = (num) => {
    return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  };

  // Cria a animação de contagem
  const animateValue = (start, end, duration) => {
    let startTimestamp = null;
    const step = (timestamp) => {
      if (!startTimestamp) startTimestamp = timestamp;
      const progress = Math.min((timestamp - startTimestamp) / duration, 1);
      const currentCount = Math.floor(progress * (end - start) + start);
      
      setCount(currentCount);
      
      if (progress < 1) {
        window.requestAnimationFrame(step);
      } else {
        setCount(end);
      }
    };
    window.requestAnimationFrame(step);
  };

  useEffect(() => {
    // Configura o observador de interseção para acionar o contador quando visível
    observerRef.current = new IntersectionObserver(
      (entries) => {
        const [entry] = entries;
        if (entry.isIntersecting && !hasAnimated.current) {
          // Inicia a contagem apenas se o elemento estiver visível e ainda não tiver sido animado
          animateValue(0, end, duration);
          hasAnimated.current = true;
        }
      },
      {
        root: null,
        threshold: 0.1,
      }
    );

    // Começa a observar o elemento contador
    if (countRef.current) {
      observerRef.current.observe(countRef.current);
    }

    // Limpa o observador quando o componente é desmontado
    return () => {
      if (observerRef.current && countRef.current) {
        observerRef.current.unobserve(countRef.current);
      }
    };
  }, [end, duration]);

  return (
    <span ref={countRef} className="counter-number">
      {formatNumber(count)}{suffix}
    </span>
  );
};

export default CounterAnimation; 